.App{margin:0 auto;margin-top:20px;padding-left:20px;padding-right:20px;min-height:100vh;font-family:Montserrat;display:flex;flex-direction:column}*,::after,::before{margin:0;box-sizing:border-box;padding:0}* :focus-visible,::after :focus-visible,::before :focus-visible{border:2px #FF6060}

a{width:100%;text-decoration:none}.sampleLoc{width:100%;height:255px;border-radius:10px;margin-top:20px;display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-start;overflow:hidden;position:relative;transition:all 1s ease}.sampleLoc:hover{box-shadow:0 0 23px #FF6060}.sampleLoc:hover :last-child{-webkit-transform:scale(1.04);transform:scale(1.04)}.sampleLoc::after{content:"";position:absolute;top:0;bottom:0;right:0;left:0;z-index:20;width:100%;height:100%;background:linear-gradient(transparent 60%, rgba(0,0,0,0.507))}.sampleLoc p{color:#fff;font-size:18px;margin-bottom:20px;margin-left:20px;margin-right:20px;z-index:50}.sampleLoc img{position:absolute;object-fit:cover;max-width:100%;max-height:100%;min-width:100%;min-height:100%;z-index:10;transition:all 0.5s ease}.sampleLoc img:hover{-webkit-transform:scale(1.04);transform:scale(1.04);box-shadow:0 0 5px #FF6060}@media screen and (min-width: 600px){.sampleLoc{margin-bottom:20px;height:300px}a{width:45%}}@media screen and (min-width: 900px){.sampleLoc{height:340px}a{width:30%}}

.announces{display:flex;flex-direction:column}@media screen and (min-width: 600px){.announces{flex-direction:row;justify-content:space-between;flex-wrap:wrap;border-radius:25px;margin-top:30px}}@media screen and (min-width: 768px){.announces{background:#f6f6f6;padding:20px 35px}}

.error404{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center;min-height:500px;color:#FF6060;flex:1 1}.error404__number{font-size:96px;font-weight:700}.error404__phrase{display:flex;flex-direction:column;align-items:center;font-size:18px;font-weight:500}.error404__homeLink{font-size:14px;font-weight:500;text-decoration:underline;margin-top:120px;text-align:center;color:#FF6060}@media screen and (min-width: 768px){.error404__number{font-size:288px;margin-top:90px}.error404__phrase{font-size:36px}.error404__phrase p{margin-left:10px}.error404__homeLink{margin-bottom:100px;font-size:18px}}@media screen and (min-width: 942px){.error404__phrase{flex-direction:row;flex-wrap:wrap}.error404__phrase p{margin-left:10px}}

.header__pic{position:relative;width:100%;height:111px;border-radius:10px;margin-top:20px;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;color:#fff;font-family:Montserrat;font-size:22px;line-height:1;overflow:hidden}.header__pic-overlay{position:absolute;background-color:#000;width:100%;height:100%;z-index:100;opacity:0.2;border-radius:10px}.header__pic-overlay :focus-visible{border:2px #FF6060}.header__pic img{border-radius:10px;position:absolute;z-index:10;width:100%;height:100%;object-fit:cover}.picAnim{-webkit-animation:picAnim 40s infinite;animation:picAnim 40s infinite}@-webkit-keyframes picAnim{0%{-webkit-transform:scale(1) translateX(0);transform:scale(1) translateX(0)}25%{-webkit-transform:scale(1.1) translateX(20px);transform:scale(1.1) translateX(20px)}50%{-webkit-transform:scale(1.2) translateX(70px);transform:scale(1.2) translateX(70px)}100%{-webkit-transform:scale(1) translateX(0);transform:scale(1) translateX(0)}}@keyframes picAnim{0%{-webkit-transform:scale(1) translateX(0);transform:scale(1) translateX(0)}25%{-webkit-transform:scale(1.1) translateX(20px);transform:scale(1.1) translateX(20px)}50%{-webkit-transform:scale(1.2) translateX(70px);transform:scale(1.2) translateX(70px)}100%{-webkit-transform:scale(1) translateX(0);transform:scale(1) translateX(0)}}.header__pic p{z-index:10000;margin-left:20px}@media screen and (min-width: 768px){.header__pic{height:223px;flex-direction:row;align-items:center;border-radius:25px}.header__pic p{font-size:40px}.header__pic img{border-radius:25px}.header__pic-overlay{border-radius:25px}}

footer{background-color:#000;height:209px;width:100%;margin-top:20px;font-size:10px;color:#fff;padding:50px 0;display:flex;flex-direction:column;justify-content:space-around;align-items:center}@media screen and (min-width: 768px){footer{font-size:24px;justify-content:flex-end;padding:0}footer p{margin-bottom:30px}footer img{margin-bottom:45px}}

header{display:flex;flex-direction:row;justify-content:space-between}header img{width:140px}header .header__buttons{display:flex;flex-direction:row;justify-content:space-between;align-items:center}header .header__buttons a{min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content}header .header__buttons span{color:#FF6060;font-family:Montserrat;font-weight:500;font-size:12px;text-transform:uppercase}header a:nth-child(1){margin-right:15px}header .underlined{text-decoration:underline;color:#FF6060}@media screen and (min-width: 768px){header img{width:211px}header .header__buttons span{font-size:18px;text-transform:none;text-transform:initial}}

.toggleDiv{background:#F6F6F6;border-radius:5px;margin-top:15px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.toggleDiv__title{display:flex;flex-direction:row;justify-content:space-between;align-items:center;border-radius:5px;padding:0 10px;font-weight:500;color:#fff;height:35px;background:#FF6060}.toggleDiv__arrow{width:15px}.toggleDiv__content{padding:15px 15px;color:#FF6060;transition:all 0.5s ease;font-weight:400;line-height:1.5}.toggleDiv .open{max-height:-webkit-fit-content;max-height:-moz-fit-content;max-height:fit-content}.toggleDiv .closed{max-height:0px;overflow:hidden;padding:0;margin:0;background:#fff}.bigFont{font-size:12px}.smallFont{font-size:12px}@media screen and (min-width: 768px){.toggleDiv{margin-top:20px;width:100%}.bigFont{font-size:24px}}

.logement{margin-top:20px;flex:1 1}.logement__gallery{width:100%;height:255px;position:relative}.logement__gallery-activePic{border-radius:10px;position:absolute;z-index:1;width:100%;height:100%;object-fit:cover}.logement__gallery>div{transition:all 0.4s ease}.logement__details{color:#FF6060;position:relative}.logement__details-descript{font-size:18px;font-weight:500;margin-top:10px;margin-right:150px}.logement__details-city{font-size:14px;font-weight:500;margin-top:10px}.logement__details-tags{display:flex;flex-direction:row;justify-content:flex-start;flex-wrap:wrap}.logement__details .tag{padding:2px 25px;border-radius:5px;margin-right:10px;background:#FF6060;color:#fff;font-size:10px;font-weight:500;margin-top:10px}.logement__details__infos{display:flex;flex-direction:row;justify-content:space-between;align-items:center;margin-top:15px}.logement__details__infos-stars img{width:16px}.logement__details__infos-name{display:flex;flex-direction:row;justify-content:flex-end;font-size:12px;font-weight:500}.logement__details__infos .names p{margin-right:10px;text-align:right}.logement__details__infos .profilePic{width:32px;height:32px;border-radius:50%;background:#C4C4C4;overflow:hidden}.logement__details__infos .profilePic img{width:32px;height:32px}@media screen and (min-width: 768px){.logement__gallery{height:415px}.logement__details-descript{font-size:36px;margin-top:35px;line-height:1.1}.logement__details-city{font-size:18px;margin-top:5px}.logement__details .tag{font-size:14px;border-radius:10px;margin-top:18px}.logement__details__infos{position:absolute;top:0;right:0;height:100%;margin-top:0px;display:flex;flex-direction:column-reverse;justify-content:space-between;align-items:flex-end}.logement__details__infos-name{font-size:18px;display:flex;flex-direction:row;align-items:center;margin-top:-10px}.logement__details__infos-stars{margin-right:0px}.logement__details__infos-stars img{width:30px;margin-left:8px}.logement__details__infos .profilePic{width:64px;height:64px}.logement__details__infos .profilePic img{width:64px;height:64px}.toggleDivs{display:flex;flex-direction:row;justify-content:space-between}.toggleDivs__logement{width:48%}}

.slide{opacity:0}.active-anim{opacity:1}.logement__gallery-leftArrow,.logement__gallery-rightArrow{background:none;border:none;z-index:1000;cursor:pointer}.logement__gallery-leftArrow{position:absolute;left:20px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.logement__gallery-rightArrow{position:absolute;right:20px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.hidden{display:none}

.toggleDiv{margin-top:20px}.APropos{flex:1 1}@media screen and (min-width: 768px){.APropos{display:flex;flex-direction:column;align-items:center}.aproposToggleDivs{width:80%}}

.picDiv{height:223px;width:100%;position:relative;overflow:hidden;border-radius:25px;margin-top:20px}.picDiv img{border-radius:25px;position:absolute;z-index:10;width:100%;height:100%;object-fit:cover}.picAnim{-webkit-animation:picAnim 40s infinite;animation:picAnim 40s infinite}@-webkit-keyframes picAnim{0%{-webkit-transform:scale(1) translateX(0);transform:scale(1) translateX(0)}25%{-webkit-transform:scale(1.1) translateX(-10px);transform:scale(1.1) translateX(-10px)}50%{-webkit-transform:scale(1.2) translateX(20px);transform:scale(1.2) translateX(20px)}100%{-webkit-transform:scale(1) translateX(0);transform:scale(1) translateX(0)}}@keyframes picAnim{0%{-webkit-transform:scale(1) translateX(0);transform:scale(1) translateX(0)}25%{-webkit-transform:scale(1.1) translateX(-10px);transform:scale(1.1) translateX(-10px)}50%{-webkit-transform:scale(1.2) translateX(20px);transform:scale(1.2) translateX(20px)}100%{-webkit-transform:scale(1) translateX(0);transform:scale(1) translateX(0)}}

